import { createElement } from '../render.js';
import { capitalizeString, getFormattedDayFromPointDate, getFormattedTimeFromPointDate, getTimeDelta } from '../utils.js';

const getDestinationById = (point, destinations) => {
  const { destination } = point;
  const destinationById = destinations.find((element) => element.id === destination);
  return destinationById;
};

const getOffersForPoint = (point, offers) => {
  const { type, offers: offersList } = point;
  const offersForType = offers.find((element) => element.type === type).offers;
  const offersForPoint = offersList.map((offer) => offersForType.find((element) => (element.id === offer)));
  return offersForPoint;
};

const createOfferTemplate = (point, offers) => {
  const offersForPoint = getOffersForPoint(point, offers);
  if (offersForPoint.length < 1) {
    return '';
  }
  return (
    `<h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
     ${offersForPoint.map(({ title, price }) => (
      `<li class="event__offer">
          <span class="event__offer-title">${title}</span>
          &plus;&euro;&nbsp;
          <span class="event__offer-price">${price}</span>
          </li>`)).join('')}
      </ul>`);
};

const createPointTemplate = (point, offers, destinations) => {
  const { basePrice, dateFrom, dateTo, isFavorite, type } = point;
  const offersTemplate = createOfferTemplate(point, offers);
  const destination = getDestinationById(point, destinations);
  return (
    `<li class="trip-events__item">
        <div class="event">
          <time class="event__date" datetime="2019-03-18">${getFormattedDayFromPointDate(dateFrom)}</time>
        <div class="event__type">
          <img class="event__type-icon" width="42" height="42" src="img/icons/${capitalizeString(type)}.png" alt="Event type icon">
        </div>
          <h3 class="event__title">${capitalizeString(type)} ${capitalizeString(destination.name)}</h3>
        <div class="event__schedule">
          <p class="event__time">
              <time class="event__start-time" datetime="2019-03-18T10:30">${getFormattedTimeFromPointDate(dateFrom)}</time>
              &mdash;
              <time class="event__end-time" datetime="2019-03-18T11:00">${getFormattedTimeFromPointDate(dateTo)}</time>
          </p>
          <p class="event__duration">${getTimeDelta(dateFrom, dateTo)}</p>
        </div>
          <p class="event__price">
            &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
          </p>
          ${offersTemplate}
          <button class="event__favorite-btn event__favorite-btn--${isFavorite ? 'active' : 'non-active'}" type="button">
            <span class="visually-hidden">Add to favorite</span>
            <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
              <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
           </svg>
          </button>
          <button class="event__rollup-btn" type="button">
            <span class="visually-hidden">Open event</span>
          </button>
          </div>
    </li>`
  );
};

export default class PointView {
  constructor({ point, offers, destinations }) {
    this.point = point;
    this.offers = offers;
    this.destinations = destinations;
  }

  getTemplate() {
    return createPointTemplate(this.point, this.offers, this.destinations);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
